@import 'common/_font&color';
@import 'common/_responsive';
@import 'common/destyle';
@import 'common/_animation';

//-------------------------------
//  All-page-common-css
//-------------------------------
html{
    /*
    計算式：10px /  デザインカンプの横幅 * 100
    画面サイズがデザインカンプ幅の時font-size = 10pxになる
    */
    font-size: calc(10 / 1400 * 100) + vw;
    // @include pc{
    //     font-size: calc(10 / 1200 * 100) + vw;
    // }
    @include tab{
        font-size: calc(10 / 768 * 100) + vw;
    }
    @include sp{
        font-size: calc(10 / 375 * 100) + vw;
    }
}
body{
    font-size: 1.6rem;
    font-family: $font;
    overflow-x: hidden;
    position: relative;
    line-height: 1;
    font-weight: 700;
    background-color: $color-bg1;
}
img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    // vertical-align: middle;
}

.wrapper{
    overflow-x: hidden;
}
.container{
    width: 960px;
    margin: 0 auto;
    @include pc-tab{
        width: 84vw;
    }
    @include tab{
        width: 84vw;
    }
}
.bg-gray{
    background-color: $color-bg2;   
}
.margin-right-cancell{
    margin-right: 0!important;
}
.margin-left-cancell{
    margin-left: 0!important;
}
.margin-top-cancell{
    margin-top: 0!important;
}
.margin-bottom-cancell{
    margin-bottom: 0!important;
}

//-------------------------------
//  header
//-------------------------------
@import 'header/_header.scss';

//-------------------------------
//  main-visual
//-------------------------------
@import '_mainvisual.scss';

// Swiperの上書き用CSS
.swiper-button-next{
    outline: none;
    &.main-next{
        right: 5rem;
        @include wide-pc{
            right: 50px;
        }
        @include tab{
            right: 1.6rem;
        }
        &:after{
            content: '\f054';
            display: block;
            font-size: 5rem;
            font-family: "Font Awesome 5 Free";
            font-weight: 900;
            color: $color-white;
            opacity: 0.4;
            transition: opacity 0.3s ease;
            @include wide-pc{
                font-size: 50px;
            }
            @include tab{
                font-size: 3.2rem;
            }
        }
        &:hover{
            &:after{
                transition: opacity 0.3s ease;
                opacity: 1;
            }
        }
    }
    &.next-common{
        right: -6.4rem;
        transition: filter 0.3s ease;
        @include wide-pc{
            right: -64px;
        }
        @include tab{
            right: -3rem;
        }
        &:after{
            content: '\f054';
            display: block;
            font-size: 3rem;
            font-family: "Font Awesome 5 Free";
            font-weight: 900;
            color: $color-white;
            opacity: 0.4;
            transition: opacity 0.3s ease;
            @include wide-pc{
                font-size: 30px;
            }
            @include tab{
                font-size: 2.4rem;
            }
        }
        &:hover{
            &:after{
                transition: opacity 0.3s ease;
                opacity: 1;
            }
        }
    }



}
.swiper-button-prev{
    outline: none;
    &.main-prev{
        left: 5rem;
        @include wide-pc{
            right: 50px;
        }
        @include tab{
            left: 1.6rem;
        }
        &:after{
            content: '\f053';
            display: block;
            font-size: 5rem;
            font-family: "Font Awesome 5 Free";
            font-weight: 900;
            color: $color-white;
            opacity: 0.4;
            transition: opacity 0.3s ease;
            @include wide-pc{
                font-size: 50px;
            }
            @include tab{
                font-size: 3.2rem;
            }
        }
        &:hover{
            &:after{
                transition: opacity 0.3s ease;
                opacity: 1;
            }
        }
    }
    &.prev-common{
        left: -6.4rem;
        @include wide-pc{
            left: -64px;
        }
        @include tab{
            left: -3rem;
        }
        &:after{
            content: '\f053';
            display: block;
            font-size: 3rem;
            font-family: "Font Awesome 5 Free";
            font-weight: 900;
            color: $color-white;
            opacity: 0.4;
            transition: opacity 0.3s ease;
            @include wide-pc{
                font-size: 30px;
            }
            @include tab{
                font-size: 2.4rem;
            }
        }
        &:hover{
            &:after{
                transition: opacity 0.3s ease;
                opacity: 1;
            }
        }
    }
}
.swiper-pagination-bullet{
    background: $color-black;
}
.swiper-pagination-bullet-active{
    background: $color-white;
}
.swiper-container-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet{
    margin: 0 1.1rem;
    @include wide-pc{
        margin: 0 11px;
    }
}
.swiper-container-horizontal>.swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction{
    bottom: 3rem;
    @include wide-pc{
        bottom: 30px;
    }
    @include tab{
        bottom: 1.6rem;
    }
}

//-------------------------------
//  link-btn
//-------------------------------
.link-btn{
    padding: 0 2rem;
    height: 4rem;
    background-color: $color-black;
    color: $color-white;
    font-size: 1.4rem;
    line-height: 1;
    display: flex;
    align-items: center;
    justify-content:center;
    border-radius: 2rem;
    transition: filter 0.3s ease;
    &:hover{
        filter: brightness(150%);
        transition: filter 0.3s ease;
    }
    @include wide-pc{
        padding: 0 20px;
        height: 40px;
        font-size: 14px;
        border-radius:40px;
    }
    i{
        font-size: 1.7rem;
        margin-right: 0.7rem;
        @include wide-pc{
            font-size: 17px;
            margin-right: 7px;
        }
    }
}
.link-btn_hSmall{
    padding: 0 2.1rem;
    height: 3rem;
    line-height: 3rem;
    font-size: 1.3rem;
    display: inline-block;
    border-radius: 1.5rem;
    @include wide-pc{
        padding: 0 21px;
        height: 30px;
        line-height: 30px;
        font-size: 13px;
        border-radius: 15px;
    }
    i{
        font-size: 1.5rem;
        margin-right: 0.6rem;
        @include wide-pc{
            font-size: 15px;
            margin-right: 6px;
        }
    }
}
//-------------------------------
//  student-media
//-------------------------------
@import 'media/_student-media.scss';

//-------------------------------
//  news
//-------------------------------
@import '_news.scss';

//-------------------------------
//  student's-contents
//-------------------------------
.student{
    padding: 7rem 0 6.4rem 0;
    @include wide-pc{
        padding: 70px 0 64px 0;
    }
    &__media{
        margin-bottom: 4rem;
    }
    &__description{
        max-width: 73rem;
        margin-left: auto;
        margin-top: 4px;
        @include wide-pc{
            max-width: 730px;
        }
        @include tab{
            padding: 1rem 0;
        }
    }

}

.movie-contents{
    width: 100%;
    position: relative;
    &__sort{
        margin-bottom: 1rem;
    }
    &__items{
        width: 100%;
    }
    &__item{
        width: 22rem;
        &:hover{
            .movie-contents__thumbnail{
                transform: scale(1.1);
                filter: brightness(120%);
                transition: filter 0.3s ease, transform 0.3s ease;
            }
        }
        @include wide-pc{
            width: 220px;
        }
        @include tab{
            width: calc((100% - 2.6rem)/2);
        }
        @include sp{
            width: 100%;
        }
    }
    &__thumbnail{
        width: 100%;
        height: 12.4rem;
        margin-bottom: 0.9rem;
        display: block;
        overflow: hidden;
        object-fit: cover;
        transform: scale(1.0);
        position: relative;
        transition: filter 0.3s ease, transform 0.3s ease;
        @include wide-pc{
            height: 124px;
            margin-bottom: 9px;
        }
        @include tab{
            height: auto;
        }
    }
    &__meta{
        margin-bottom: 0.9rem;
        @include wide-pc{
            margin-bottom: 9px;
        }
    }
    &__post-time{
        margin-right: 0.5rem;
        @include wide-pc{
            margin-right: 5px;
        }
    }
    &__title{
        @include font-style(1.5rem, $color-white, 700);
        line-height: 1;
        display: -webkit-box;
        overflow: hidden;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        margin-bottom: 0.6rem;
        @include wide-pc{
            font-size: 15px;
            margin-bottom: 6px;
        }
    }
}

.ly-movie-top{
    margin-right: 2.6rem;
}

