//-------------------------------
//  Class(授業一覧)
//-------------------------------
.class{
    padding: 7.1rem 0;
    @include wide-pc{
        padding: 71px 0;
    }
    &__inner{
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        @include tab{
            &:after{
                content: '';
                display: block;
                width: 25.4rem;
            }
        }
    }
    &__term-list{
        padding-left: 5rem;
        border-right: solid 1px $color-border;
        @include wide-pc{
            padding-left: 50px;
        }
        @include tab{
            border-right: none;
            margin-bottom: 6rem;
        }
    }
}

//-------------------------------
//  List-tag
//-------------------------------
.term-list{
    width: 25.4rem;
    @include wide-pc {
        width: 254px;
    }
    &__title{
        @include font-style(1.4rem, $color-white, 700);
        line-height: 2.6rem;
        height: 2.6rem;
        background-color: $color-button-bg;
        padding: 0 2.3rem;
        display: inline-block;
        margin-bottom: 3rem;
        @include wide-pc{
            font-size: 14px;
            height: 26px;
            padding: 0 23px;
            margin-bottom: 30px;
            line-height: 26px;
        }
        i{
            font-size: 1.2rem;
            margin-right: 0.3rem;
            @include wide-pc {
                font-size: 12px;
                margin-right: 3px;
            }
        }
    }
    &__item{
        margin-bottom: 1rem;
        @include wide-pc{
            margin-bottom: 10px;
        }
    }
}
.list{
    &__link{
        padding: 0 1.5rem;
        height: 4rem;
        background-color: $color-black;
        color: $color-white;
        font-size: 1.5rem;
        line-height: 4rem;
        display: inline-block;
        border-radius: 2rem;
        transition: filter 0.3s ease;
        &:hover{
            filter: brightness(150%);
            transition: filter 0.3s ease;
        }
        @include wide-pc{
            padding: 0 15px;
            height: 40px;
            font-size: 15px;
            border-radius:40px;
            line-height: 40px;
        }
        i{
            font-size: 1.3rem;
            margin-right: 0.4rem;
            @include wide-pc{
                font-size: 13px;
                margin-right: 4px;
            }
        }
    }
}

//-------------------------------
//  Search-results
//-------------------------------
.search-results{
    &__head{
        padding-top: 7rem;
        text-align: center;
        @include wide-pc{
            padding-top: 70px;
        }
    }
    &__title{
        @include font-style(2.2rem, $color-white, 700);
        @include wide-pc{
            font-size: 22px;
        }
        @include tab{
            font-size: 2rem;
        }
        &:before{
            content: '\f002';
            @include font-style(2.6rem, $color-white, 700);
            font-family: "Font Awesome 5 Free";
            @include wide-pc{
                font-size: 26px;
            }
            @include tab{
                font-size: 1.6rem;
            }
        }
    }
}


//-------------------------------
//  Archive
//-------------------------------
.archive{
    padding: 7rem 0;
    @include wide-pc{
        padding: 70px 0;
    }
}
.article{
    &__items{
        width: 60rem;
        margin: 0 auto;
        @include wide-pc{
            width: 600px;
        }
        @include tab{
            width: 100%;
        }
    }
    &__item{
        width: 100%;
        padding-bottom: 1.6rem;
        border-bottom: solid 1px $color-border;
        @include wide-pc{
            padding-bottom: 16px;
        }
    }
    &__link{
        display: block;
    }
    &__post-time{
        margin-bottom: 1.5rem;
        @include wide-pc {
            margin-bottom: 15px;
        }
    }
    &__title{
        @include font-style(1.5rem, $color-white, 700);
        line-height: 1;
        display: -webkit-box;
        overflow: hidden;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        @include wide-pc{
            font-size: 15px;
        }
    }
}
.pager{
    &__navi{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        text-align: center;
        gap: 1.5rem;
        li{
            width: 3rem;
            height: 3rem;
            background-color: $color-black;
            color: $color-white;
            display: flex;
            justify-content: center;
            align-items: center;
            &.current{
                opacity: 0.6;
            }
        }
    }
}
.item-margin{
    margin-bottom: 1.6rem;
    &:last-child{
        margin-bottom: 7rem;
    }
    @include wide-pc {
        margin-bottom: 16px;
        &:last-child{
            margin-bottom: 70px;
        }
    }
}

//-------------------------------
//  Single
//-------------------------------
.single{
    padding: 7rem 0;
    @include wide-pc{
        padding: 70px 0;
    }
    &__post-time{
        margin-bottom: 1.5rem;
        @include wide-pc {
            margin-bottom: 15px;
        }
    }
    &__title{
      margin-bottom: 4rem;
      @include wide-pc {
          margin-bottom: 40px;
      }
    }

    article{
        width: 60rem;
        margin: 0 auto;
        @include wide-pc{
            width: 600px;
        }
        @include tab{
            width: 100%;
        }
    }
    h1{
        @include font-style(2.4rem, $color-white, 700);
        line-height: 1;
        display: -webkit-box;
        overflow: hidden;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        @include wide-pc{
            font-size: 24px;
        }
    }
    h2{
        @include font-style(2rem, $color-white, 700);
        line-height: 1;
        display: -webkit-box;
        overflow: hidden;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        margin-bottom: 3.2rem;
        @include wide-pc{
            font-size: 20px;
            margin-bottom: 32px;
        }
    }
    p{
        @include font-style(1.5rem, $color-white, 500);
        line-height: 1.8;
        margin-bottom: 4.8rem;
        @include wide-pc {
            font-size: 15px;
            margin-bottom: 48px;
        }
        @include tab{
            font-size: 1.2rem;
        }
    }
}

//-------------------------------
//  Single-custom
//-------------------------------
@import 'single/_single-custom.scss';

